.clock {
  height: 220px;
  width: 220px;
  border-radius: 110px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative; }
  .clock__minute, .clock__hour, .clock__indicator {
    position: absolute;
    left: calc(50% - 1px);
    width: 2px;
    background: #f4eed7;
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
    z-index: 2;
    border-radius: 1px; }
  .clock__minute {
    height: 90px;
    background: #4b9aaa;
    margin-top: 20px;
    opacity: 0.75;
    -webkit-animation: time 1.2s linear infinite;
            animation: time 1.2s linear infinite;
    -webkit-animation-play-state: paused;
            animation-play-state: paused; }
  .clock__indicator {
    height: 108px;
    border-top: 2px solid #4b9aaa;
    background: none; }
  .clock__indicator:nth-of-type(5n) {
    opacity: 1;
    height: 103px;
    border-top: 7px solid #f4eed7; }
  .clock__axis {
    background: #4b9aaa;
    width: 5px;
    height: 5px;
    border-radius: 3px;
    position: absolute;
    z-index: 4;
    top: 107px; }

